<template>
    <div class="index">
       <Navheader></Navheader>
        <div class="Mess_content">
            <div class="mes_title"> PROFESSIONAL INFORMATION</div>
            <!-- <div class="trem_M" @click="jump_detail()">
                <div class="trem_tit">
                    <img src="@/assets/index/shoufei.png" alt=""/>     
                    <p class="item_tit"></p>    
                </div>
                <p class="item_con">20194300</p>
                <p class="item_date">2018-11-02</p>
            </div> -->
            <div class="trem_M" v-for="(item,index) in inforList" @click="jump_detail($event)" :index='index'>
                    <div class="trem_tit">
                        <img src="@/assets/index/shoufei.png" alt=""/>     
                        <p class="item_tit">{{item.F_DATASOURCE}}</p>    
                    </div>
                <p class="item_con item_con_M">{{item.F_CONTENT}}</p>
                <p class="item_date">{{item.F_DATE}}</p>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
import Navheader from '@/components/Navheader'
export default {
  name: 'Index',
  data () {
    return {
      msg: 'Welcome to Your  A1',
      inforList:[
          {F_DATASOURCE: "", F_DATE: '2018-11-02',F_CONTENT: "20194300ITVIP"},
          {F_DATASOURCE: "A", F_DATE: '2018-10-26',F_CONTENT: "22",F_CONTENT2:"153",F_CONTENT3:"2",F_CONTENT4:"360%6"},
          {F_DATASOURCE: "......", F_DATE: '2018-10-18',F_CONTENT: "584.67%70.0812"},
          {F_DATASOURCE: "ODIQDIIQDLPQDIE", F_DATE: '2018-10-09',F_CONTENT: "4-5%15%37%15-2015-20"},
          {F_DATASOURCE: "21", F_DATE: '2018-09-30',F_CONTENT: "4272018522.28621720()928"},
          {F_DATASOURCE: "", F_DATE: '2018-09-29',F_CONTENT: "98868////CTA2018-7.18%-0.80%8-2.83%CTA"}
        ]
    }
  },
  components:{Navheader},
  mounted () {
    //this.changeCai() 
    //this.enterJin()     
  },
  methods: {    
    changeCai (){
      $(".caifu_lists").hover(function(){
          $(this).find(".lists_title").hide();
           $(this).find(".lists_detail").show();
      },function(){
          $(this).find(".lists_title").show();
           $(this).find(".lists_detail").hide();
      })
    },
    jump_detail(event){
       // console.log(this.inforList[0].F_CONTENT)
        var index=event.currentTarget.getAttribute('index');//
       // console.log(index)
        //console.log(this.inforList[index].F_CONTENT)
        if(!this.inforList[index].F_CONTENT2 == false){
            this.$router.push({
                path:'/detail',
                name:'detail',
                query:{
                    title:encodeURIComponent(this.inforList[index].F_DATASOURCE),
                    date: encodeURIComponent(this.inforList[index].F_DATE),
                    content:encodeURIComponent(this.inforList[index].F_CONTENT),
                    content2:encodeURIComponent(this.inforList[index].F_CONTENT2),
                    content3:encodeURIComponent(this.inforList[index].F_CONTENT3),
                    content4:encodeURIComponent(this.inforList[index].F_CONTENT4)
                }
            })    
        }else{
            this.$router.push({
                path:'/detail',
                name:'detail',
                query:{
                    title:encodeURIComponent(this.inforList[index].F_DATASOURCE),
                    date: encodeURIComponent(this.inforList[index].F_DATE),
                    content:encodeURIComponent(this.inforList[index].F_CONTENT)
                }
            }) 
        }
        
    }       
  },
  created(){
     // this.getData()
  }
}
</script>
<style scoped lang="stylus">
#zhichiBtnBox{
    display: none;
}
.index{
  width:100%;
  overflow:hidden;
  min-width:1200px;
 // position relative
}
.Mess_content
    padding 45px 18.75% 23px
    box-sizing border-box
    .mes_title
        color #B09A7D
        font-size 30px
        font-weight 600
        border-bottom 1px solid #CCCCCC
        padding 16px 0
.trem_M
    border-bottom 1px dashed #ccc
    padding-bottom 12px
    .trem_tit
        overflow hidden
        padding 20px 0 10px
        img
            float left
            height 19px
        .item_tit
            color #383838
            font-size 18px
            font-weight 600
            padding-left 8px
            float left
            width 80%
            display -webkit-box
            overflow hidden
            white-space normal !important
            text-overflow ellipsis
            word-wrap break-word
            -webkit-line-clamp 1
            -webkit-box-orient vertical
    .item_con
        color #383838
        font-size 15px
        line-height 1.8
        display -webkit-box
        overflow hidden
        white-space normal !important
        text-overflow ellipsis
        word-wrap break-word
        -webkit-line-clamp 2
        /* autoprefixer: off */
        -webkit-box-orient vertical
        /* autoprefixer: on */
    .item_date    
        text-align right
        color #888888
        font-size 14px
        padding-top 15px
.trem_M:last-of-type
    border none
.trem_M:hover
    cursor pointer
</style>


